// type
import { ExtensionsType } from 'types/Extensions'
import { MessageSetDataUrlType } from 'types/AppState'

/**
 * createMessageSetDataUrlType
 * @param {string} targetExtension - 
 * @return {MessageSetDataUrlType} - createMessageSetDataUrlType
 */
export const createMessageSetDataUrlType = ({
  targetExtension = '',
  data = ''
}: {
  targetExtension: '' | ExtensionsType
  data: string
}): MessageSetDataUrlType => {
  switch (targetExtension) {
    case 'atlas':
      return {
        type: 'SetDataUrlAtlas',
        data
      }

    case 'json':
      return {
        type: 'SetDataUrlJson',
        data
      }
    case 'skel':
      return {
        type: 'SetDataUrlSkel',
        data
      }
    case 'png':
      return {
        type: 'SetDataUrlPng',
        data
      }
    default:
      return {
        type: 'FailedSetDataUrl'
      }
  }
}
